<template>
  <div v-show="disponibilidad" style="display:inline-block">
    <calculos
      banco="hsbc"
      :tasa="tasa"
      :valor-del-inmueble="valorDelInmueble"
      :factor="factor"
      :factor-seguro-de-vida="factorSeguroDeVida"
      :factor-seguro-de-danos="factorSeguroDeDanos"
      :comision-administracion="comisionAdministracion"
      :iva="iva"
      :monto-de-credito="montoDeCredito"
      :disponibilidad="disponibilidad"
      :enganche="enganche"
      :porcentaje-de-credito="porcentajeDeCreditoAsignado"
      :tipo-de-ingreso="tipoDeIngreso"
    />
  </div>
</template>
<script>
import Calculos from "../Calculos.vue";
import { mapState } from "vuex";

export default {
  name: "hsbc",
  components: {
    Calculos
  },
  // props: [
  //   "plazo",
  //   "tipoDePagoSeleccionado",
  //   "valorDelInmueble",
  //   "productoSeleccionado",
  //   "montoDeCreditoPorModificacion",
  //   "tipoDeIngreso"
  // ],
  data() {
    return {
      factorSeguroDeVida: 0.00033,
      iva: 0,

      productos: [
        {
          nombre: "Comprar mi Casa",
          porcentajeMaximoDeCredito: 0.9,
          plazosFijos: [true, true, true],
          plazosCrecientes: [true, true, true],
          // factoresFijos: [0.595124579, 0.281339901, 0.152683088],
          factoresFijos: [0.595124579, 0.281339901, 0.152683088],
          factoresCrecientes: [0.511515151, 0.188177339, 0.049852216],
          tasa: [0.099, 0.1015, 0.1015],
          factorSeguroDeDanos: 0.0002965,
          comisionAdministracion: 0
        }
      ]
    };
  },

  computed: {
    disponibilidad() {
      var disponibilidadDePlazo = false;
      var disponibilidadDeMontoDeCredito = true;
      var tipoDePlazos;
      if (this.tipoDePagoSeleccionado == 0) tipoDePlazos = "plazosFijos";
      if (this.tipoDePagoSeleccionado == 1) tipoDePlazos = "plazosCrecientes";
      if (this.productoAsignado[tipoDePlazos][this.plazoSeleccionado])
        disponibilidadDePlazo = true;

      if (this.montoDeCreditoPorModificacion > this.maximoDeCredito) {
        disponibilidadDeMontoDeCredito = false;
      }

      return (
        disponibilidadDePlazo &&
        this.valorNumericoDelInmueble &&
        disponibilidadDeMontoDeCredito
      );
    },
    porcentajeDeCreditoAsignado() {
      if (!isNaN(this.montoDeCreditoPorModificacion)) {
        return (
          (this.montoDeCreditoPorModificacion * 100) /
          this.valorNumericoDelInmueble /
          100
        );
      } else {
        return this.porcentajeMaximoDeCredito;
      }
    },
    productoAsignado() {
      return this.productos.filter(producto => {
        return producto.nombre == this.productoSeleccionado;
      })[0]; // devolver el primer objeto para no obtener un array
    },
    tasa() {
      return this.productoAsignado.tasa[this.plazoSeleccionado];
    },
    factor() {
      var tipoDeFactores;
      if (this.tipoDePagoSeleccionado == 0) tipoDeFactores = "factoresFijos";
      if (this.tipoDePagoSeleccionado == 1)
        tipoDeFactores = "factoresCrecientes";
      return this.productoAsignado[tipoDeFactores][this.plazoSeleccionado];
    },
    factorSeguroDeDanos() {
      return this.productoAsignado.factorSeguroDeDanos;
    },
    comisionAdministracion() {
      return this.productoAsignado.comisionAdministracion;
    },
    valorNumericoDelInmueble() {
      return parseFloat(this.valorDelInmueble);
    },
    porcentajeMaximoDeCredito() {
      if (this.tipoCredito == "cofinavit" || this.tipoCredito == "apoyoInfonavit") {
        return 0.95;
      } else {
        return this.productoAsignado.porcentajeMaximoDeCredito;
      }
    },
    maximoDeCredito() {
      return this.valorNumericoDelInmueble * this.porcentajeMaximoDeCredito;
    },
    montoDeCredito() {
      return this.valorNumericoDelInmueble * this.porcentajeDeCreditoAsignado; 
    },
    enganche() {
      return this.valorNumericoDelInmueble - this.montoDeCredito;
    },
    ...mapState([
      "plazoSeleccionado",
      "tipoDePagoSeleccionado",
      "valorDelInmueble",
      "productoSeleccionado",
      "montoDeCreditoPorModificacion",
      "tipoDeIngreso",
      "cuantoCuentas",
      "tipoCredito"
    ])
  }
};
</script>

