<template>
  <div>
    <div v-if="tipoDeIngreso == 'Asalariado'">
      <span class="checkbox-apoyo span">Quieres utilizar:</span>
      <v-checkbox
        @click="seleccionarApoyo('cofinavit')"
        label="Cofinavit"
        class="checkbox-apoyo"
        v-model="cofinavit"
        color="#13d8f2"
      ></v-checkbox>
      <v-checkbox
        @click="seleccionarApoyo('apoyoInfonavit')"
        label="Apoyo Infonavit"
        class="checkbox-apoyo"
        v-model="apoyoInfonavit"
        color="#13d8f2"
      ></v-checkbox>
      <v-checkbox
        @click="seleccionarApoyo('ninguno')"
        label="Ningúno"
        class="checkbox-apoyo"
        v-model="ninguno"
        color="#13d8f2"
      ></v-checkbox>
    </div>
    <br>
    <div>
      <v-text-field
        v-show="cofinavit && tipoDeIngreso == 'Asalariado'"
        class="no-margin"
        v-model="number"
        prefix="$"
        label="Con cuanto cuentas en cofinavit"
        @input="hideKeyboardStartTimeOut"
        color="#13D8F2"
      ></v-text-field>
      <div id="apoyoinputkeyboard"></div>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      cofinavit: false,
      apoyoInfonavit: false,
      ninguno: false,

      montoDeApoyo: "",
      number: "",
      hideKeyboardTime: ""
    };
  },
  computed: {
    montoDeApoyoComputado() {
      return this.montoDeApoyo;
    },
    ...mapState(["tipoDeIngreso"])
  },
  methods: {
    seleccionarApoyo(apoyo) {
      switch (apoyo) {
        case "cofinavit":
          this.cofinavit = true;
          this.apoyoInfonavit = false;
          this.ninguno = false;
          this.actualizarTipoCredito("cofinavit");
          this.actualizarApoyo(this.montoDeApoyoComputado);
          break;

        case "apoyoInfonavit":
          this.cofinavit = false;
          this.apoyoInfonavit = true;
          this.ninguno = false;
          this.actualizarTipoCredito("apoyoInfonavit");
          this.actualizarApoyo("");
          break;

        case "ninguno":
          this.cofinavit = false;
          this.apoyoInfonavit = false;
          this.ninguno = true;
          this.actualizarTipoCredito("ninguno");
          this.actualizarApoyo("");
          break;
      }

      setTimeout(() => {
        this.$forceUpdate();
      }, 1000);
    },
    currencyInput() {
      let number = parseFloat(this.number.replace(/,/g, "") || 0);
      this.montoDeApoyo = number;
      // this.number = parseFloat(number).toLocaleString();
      this.number = this.numberWithCommas(parseFloat(number));
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },
    hideKeyboardStartTimeOut() {
      var field = document.createElement("input");
      field.setAttribute("type", "text");
      let inputContainer = document.getElementById("apoyoinputkeyboard");
      inputContainer.innerHTML = "";
      inputContainer.appendChild(field);
      inputContainer.style = "height:0px;width:100%";
      field.setAttribute("readonly", "readonly");

      clearTimeout(this.hideKeyboardTime);
      this.hideKeyboardTime = setTimeout(() => {
        // document.getElementById("toHideKeyboard").focus();
        field.focus();
        field.setAttribute("style", "display:none;width:100%");
      }, 2000);
    },
    ...mapMutations(["actualizarApoyo", "actualizarTipoCredito"])
  },
  watch: {
    montoDeApoyo() {
      this.actualizarApoyo(this.montoDeApoyo);
    },
    number() {
      this.currencyInput();
    }
  },
};
</script>
<style>
.v-input.checkbox-apoyo.v-input--selection-controls.v-input--checkbox.theme--light {
  display: inline-block;
  margin: 0px;
  margin-right: 20px;
}

.v-input.checkbox-apoyo.v-input--selection-controls.v-input--checkbox.theme--light
  * {
  margin: 0;
}

.v-input.checkbox-apoyo.v-input--selection-controls.v-input--checkbox.theme--light
  .v-messages.theme--light {
  display: none;
}

.checkbox-apoyo.span {
  position: relative;
  display: inline-block;
  margin-right: 20px;
  bottom: 8px;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.54);
}

@media only screen and (max-width: 600px) {
  .checkbox-apoyo.span {
    display: block;
    text-align: left;
    font-size: 16px;
    color: rgba(0, 0, 0, 0.54);
  }
  .checkbox-apoyo .v-input__control .v-input__slot label.v-label {
    font-size: 10px !important;
  }
}
</style>
