<template>
  <div  :class="{'mb-static':type == 'static','mb-fixed':type == 'fixed'}">
    <v-tabs v-model="tipoDePagoSeleccionadoLocal" grow slider-color="#13D8F2">
      <v-tab style="font-size: 13px">Pagos Fijos</v-tab>
      <v-tab style="font-size: 13px">Pagos Crecientes</v-tab>
    </v-tabs>
  </div>
</template>
<script>
export default {};
</script>
<script>
import { mapState, mapMutations } from "vuex";

export default {
  props:["type"],
  data() {
    return {
      tipoDePagoSeleccionadoLocal: 0,
      scroll: 0
    };
  },
  computed: {
    
    ...mapState(["tipoDePagoSeleccionado"])
  },
  methods: {
    ...mapMutations(["actualizarTipoDePagoSeleccionado"])
  },
  watch: {
    tipoDePagoSeleccionadoLocal() {
      this.actualizarTipoDePagoSeleccionado(this.tipoDePagoSeleccionadoLocal);
    },
    tipoDePagoSeleccionado(){
      this.tipoDePagoSeleccionadoLocal = this.tipoDePagoSeleccionado;
    }
  },
  mounted(){
      this.tipoDePagoSeleccionadoLocal = this.tipoDePagoSeleccionado;
  }
 
};
</script>
<style scoped>
  .mb-static {
    margin-bottom: 35px
  }
  .mb-fixed{
    margin-bottom: 60px
  }
</style>
