<template>
  <div>
    <v-bottom-nav :active.sync="plazoSeleccionadoLocal" :value="true" absolute>
      <v-btn color="#13D8F2" flat value="2">
        <span>Años</span>
        <span class="number-years">20</span>
      </v-btn>
      <v-btn color="#13D8F2" flat value="1">
        <span>Años</span>
        <span class="number-years">15</span>
      </v-btn>
      <v-btn color="#13D8F2" flat value="0">
        <span>Años</span>
        <span class="number-years">10</span>
      </v-btn>
    </v-bottom-nav>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      plazoSeleccionadoLocal: "2"
    };
  },
  computed:{
    ...mapState(["plazoSeleccionado"])
  },
  methods: {
    ...mapMutations(["actualizarPlazoSeleccionado"])
  },
  watch: {
    plazoSeleccionadoLocal() {
      this.actualizarPlazoSeleccionado(this.plazoSeleccionadoLocal);
    },
    plazoSeleccionado(){
      this.plazoSeleccionadoLocal = this.plazoSeleccionado
    }
  },
  mounted(){
      this.plazoSeleccionadoLocal = this.plazoSeleccionado

  }
};
</script>
