import store from '../store.js'

import Request from './request'

const backend = store.state.backend;

export default {
  get(endpoint) {
    return Request.get(backend + endpoint );
  },
  post(endpoint, data) {
    return Request.post(backend + endpoint, data );
  },
  // put(endpoint, id, data) {
  //   return Request.put(backend + endpoint + id, data, );
  // },
  // delete(endpoint, id) {
  //   return Request.delete(backend + endpoint + id, );
  // },

  // subir archivos 

  file(endpoint, data) {
    return Request.file(backend + endpoint, data);
  },
}