<template>
  <v-layout>
    <v-dialog v-model="dialog" persistent width="700" fullscreen >
      <v-card >
        <v-layout row wrap>
          <v-flex class="width-3 center-margin fixed-width pt-3">
              <div
                v-if="status == 2"
                class="headline center"
              >Tramitar tu crédito en linea es fácil y rápido, regístrate.</div>
              <!--CAPTURO EL EVENTO Y CIERRO EL DIALOGO-->
              <Tabs @closingDialog="closeDialog" :bancoId="bancoId" :banco="banco"/>
              <!--<v-card-actions style="padding-right: 40px;">
                <v-spacer></v-spacer>
                <v-btn color="#13D8F2" @click="closeDialog">Volver</v-btn>
                <v-btn type="submit" color="#13D8F2">Enviar</v-btn>
              </v-card-actions>-->
          </v-flex>
        </v-layout>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import Tabs from "./modalTransact/Tabs.vue";
import { mapState } from "vuex";
export default {
  props: ["dialog", "status", "banco", "creditoPreautorizado", "bancoId"],
  components: {
    Tabs
  },
  data() {
    return {
      tabs: 0,

      // datos del usuario
      name: "",
      email: "",
      phone: "",
      password: "",

      message: "",
      alert: false,
      show: false,

      dialog: this.dialog,
      rules: {
        required: value => !!value || "Requerido.",
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || "Correo no valido.";
        },
        number: value => {
          const pattern = /^([0-9])*$/;
          return pattern.test(value) || "Teléfono invalido.";
        }
      },

      // datos de envío

      sending: false,
      response: ""
    };
  },
  methods: {
    closeDialog() {
      this.$emit("closeDialog");
    }
  },
  computed: {
    ...mapState([
      "tipoDeIngresoSeleccionado",
      "ingresoMensual",
      "bancos",
      "backend",
      "tipoDePagoSeleccionado",
      "plazoSeleccionado",
      "cuantoCuentas",
      "tipoCredito"
    ])
  }
};
</script>
<style scoped>
.headline {
  color: #13d8f2;
  font-weight: bold;
  padding: 0px !important;
}

.center {
  text-align: center !important;
  margin-left: auto;
}

.fixed-width {
  width: 100%;
}

.background-white {
  background-color: #ffffff;
}

.center-margin {
  margin: 0 auto;
}

.right-align img {
  margin-right: 10%;
}
</style>
<style>
.tab-transact .v-tabs__wrapper .v-tabs__container {
  height: 140px !important;
}
.tab-transact .v-tabs__div.primary--text {
  height: 140px !important;
}

.container-tab .v-card {
  box-shadow: none !important;
}

.v-card.dialog-100vh {
  min-height: calc(100vh + 30%);
}
</style>
