<template>
  <div>
    <div @mouseover="infoMontoDeCredito = true" @mouseleave="infoMontoDeCredito = false">
      <p
        v-show="infoMontoDeCredito"
        style="color:#13D8F2"
        class="placeholder-info"
      >Si necesitas un credito menor, teclealo aquí</p>
      <div v-if="!modificarMontoDeCreditoLocal">
        <v-text-field
          class="no-margin"
          v-model="localNumber"
          @focus="modificarMontoDeCreditoLocalMethod()"
          prefix="$"
          label="Monto de Crédito"
          append-icon="help"
          color="#13D8F2"
          :class="{'background-focus':infoMontoDeCredito}"
        ></v-text-field>
      </div>
      <div v-else>
        <div>
          <v-text-field
            id="montoDeCreditoPorModificacion"
            prefix="$"
            class="no-margin"
            v-model="number"
            @blur="validarModificacionDeMontoCredito"
            label="Monto de Crédito"
            append-icon="help"
            color="#13D8F2"
            @input="hideKeyboardStartTimeOut"
            :class="{'background-focus':infoMontoDeCredito}"
          ></v-text-field>
        </div>
      </div>
      <div id="montodecreditoinputkeyboard"></div>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      infoMontoDeCredito: false,
      montoDeCreditoPorModificacionLocal: "",
      modificarMontoDeCreditoLocal: false,

      number: "",
      hideKeyboardTime: ""
    };
  },
  computed: {
    localNumber() {
      return parseFloat(
        this.montoDeCreditoMaximoFicitcioLocal
      ).toLocaleString();
    },
    montoDeCreditoMaximoFicitcioLocal() {
      if (this.montoDeCreditoMaximoFicitcio) {
        this.actualizarMontoDeCreditoPorModificacion(
          this.montoDeCreditoMaximoFicitcio
        );
        return this.montoDeCreditoMaximoFicitcio.toFixed(2);
      } else {
        this.actualizarMontoDeCreditoPorModificacion(
          this.valorDelInmueble * 0.9
        );
        return (this.valorDelInmueble * 0.9).toFixed(2);
      }
    },
    ...mapState([
      "modificarMontoDeCredito",
      "valorDelInmueble",
      "montoDeCreditoPorModificacion",
      "cuantoCuentas",
      "montoDeCreditoMaximoFicitcio"
    ])
  },
  methods: {
    validarModificacionDeMontoCredito() {
      if (
        this.montoDeCreditoPorModificacionLocal == undefined ||
        this.montoDeCreditoPorModificacionLocal == "" ||
        this.montoDeCreditoPorModificacionLocal == 0
      ) {
        this.actualizarModificarMontoDeCredito(false);
        this.actualizarMontoDeCreditoPorModificacion(undefined);
      }
    },
    modificarMontoDeCreditoLocalMethod() {
      this.modificarMontoDeCreditoLocal = true;
      setTimeout(() => {
        document.getElementById("montoDeCreditoPorModificacion").focus();
      }, 100);
    },
    ...mapMutations([
      "actualizarModificarMontoDeCredito",
      "actualizarMontoDeCreditoPorModificacion"
    ]),
    currencyInput() {
      let number = parseFloat(this.number.replace(/,/g, "") || 0);
      this.montoDeCreditoPorModificacionLocal = number;
      this.number = this.numberWithCommas(parseFloat(number));
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },
    hideKeyboardStartTimeOut() {
      var field = document.createElement("input");
      field.setAttribute("type", "text");
      let inputContainer = document.getElementById(
        "ingresomensualinputkeyboard"
      );
      inputContainer.innerHTML = "";
      inputContainer.appendChild(field);
      inputContainer.style = "height:0px;width:100%";
      field.setAttribute("readonly", "readonly");

      clearTimeout(this.hideKeyboardTime);
      this.hideKeyboardTime = setTimeout(() => {
        // document.getElementById("toHideKeyboard").focus();
        field.focus();
        field.setAttribute("style", "display:none;width:100%");
      }, 2000);
    }
  },
  watch: {
    montoDeCreditoPorModificacionLocal() {
      this.actualizarMontoDeCreditoPorModificacion(
        this.montoDeCreditoPorModificacionLocal
      );
      console.log(
        this.montoDeCreditoPorModificacionLocal,
        this.montoDeCreditoPorModificacion
      );
    },
    montoDeCreditoPorModificacion() {
      if (
        this.montoDeCreditoPorModificacion == undefined ||
        this.montoDeCreditoPorModificacion == "" ||
        this.montoDeCreditoPorModificacion == 0
      ) {
        this.montoDeCreditoPorModificacionLocal = "";
        this.modificarMontoDeCreditoLocal = false;
      }
    },
    modificarMontoDeCredito() {
      this.modificarMontoDeCreditoLocal = this.modificarMontoDeCredito;
    },
    number() {
      this.currencyInput();
    }
  }
};
</script>

