<template>
  <div v-show="false">
    Máximo de crédito: {{porcentajeDeCredito * 100}} %
    <br>
    Credito: $ {{montoDeCredito}}
    <br>
    <br>
    Mensualidad: $ {{mensualidad.toFixed()}}
    <br>
    Tasa: {{tasa * 100}} %
    <br>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "calculos",
  props: [
    "tasa",
    "factorInterez",
    "factor",
    "factorSeguroDeVida",
    "factorSeguroDeDanos",
    "seguroDeDanos",
    "seguroDeDesempleo",
    "comisionAdministracion",
    "iva",
    "banco",
    "montoDeCredito",
    "disponibilidad",

    "enganche",
    "porcentajeDeCredito",

    "tipoDeIngreso",

    "ochentaBanamex"
  ],
  methods: {
    ...mapMutations(["asignarBanco", "actualizarMontoDeCreditoMaximoFicitcio"])
  },
  computed: {
    mensualidad() {
      var montoDeCreditoTotal = this.montoDeCredito;
      var seguroDeVida;

      var cuantoCuentas = this.cuantoCuentas;

      if (this.tipoCredito == "cofinavit" && cuantoCuentas) {
        if (cuantoCuentas >= this.valorDelInmueble * 0.15) {
          montoDeCreditoTotal =
            this.montoDeCreditoPorModificacion ||
            this.valorDelInmueble * 1.1 - cuantoCuentas; // igual o mayor 15%
        } else if (cuantoCuentas <= this.valorDelInmueble * 0.05) {
          montoDeCreditoTotal =
            this.montoDeCreditoPorModificacion || this.valorDelInmueble * 0.95;
        } else if (
          cuantoCuentas > this.valorDelInmueble * 0.05 &&
          cuantoCuentas <= this.valorDelInmueble * 0.15
        ) {
          montoDeCreditoTotal =
            this.montoDeCreditoPorModificacion ||
            this.valorDelInmueble - cuantoCuentas;
        }

        this.actualizarMontoDeCreditoMaximoFicitcio(montoDeCreditoTotal);
      } else {
        this.actualizarMontoDeCreditoMaximoFicitcio(undefined);
        montoDeCreditoTotal =
          this.montoDeCreditoPorModificacion || this.montoDeCredito;
      }

      // en caso de santander

      if (this.banco == "santander") {
        if (this.montoDeCredito < this.valorDelInmueble * 0.78) {
          var primaUnica = this.montoDeCredito * 0.03154;
          montoDeCreditoTotal = this.montoDeCredito + primaUnica;
          seguroDeVida = 0;
        } else {
          seguroDeVida = montoDeCreditoTotal * this.factorSeguroDeVida;
        }
      } else {
        seguroDeVida = montoDeCreditoTotal * this.factorSeguroDeVida;
      }

      var interes;
      var seguroDeDanos;

      if (this.banco == "banorte") {
        interes = montoDeCreditoTotal * parseFloat(this.factorInterez);
        seguroDeDanos = montoDeCreditoTotal * this.factorSeguroDeDanos;
      } else if (this.banco == "banamex") {
        interes = (montoDeCreditoTotal * this.tasa) / 12;

        seguroDeDanos =
          Math.max(this.valorDelInmueble * 0.8, montoDeCreditoTotal) *
          this.factorSeguroDeDanos;

        // seguroDeDanos = (this.valorDelInmueble * .8) * Math.max(this.factorSeguroDeDanos, );

        // if (montoDeCreditoTotal <= 1600000) {
        //   seguroDeDanos = 480;
        // } else {
        // }

        // if (this.ochentaBanamex == true) {
        //   seguroDeDanos = montoDeCreditoTotal * this.factorSeguroDeDanos;
        // } else {
        //   seguroDeDanos = montoDeCreditoTotal * this.factorSeguroDeDanos;

        // }
      } else {
        // los demas
        interes = (montoDeCreditoTotal * this.tasa) / 12;
        seguroDeDanos = this.valorDelInmueble * this.factorSeguroDeDanos;
      }

      var amortizacion = interes * this.factor;
      var pagoMensual = interes + amortizacion;

      // en caso de banregio

      var seguroDeDesempleo = 0;
      if (this.banco == "banregio") {
        seguroDeDesempleo = this.seguroDeDesempleo;
        seguroDeDanos = this.seguroDeDanos;
      }

      // console.log(this.banco);
      // console.log("tasa: " + this.tasa);
      // console.log("monto de credito: " + montoDeCreditoTotal);
      // console.log("interes: " + interes);
      // console.log("amortizacion: " + amortizacion);
      // console.log("pagoMensual: " + pagoMensual);
      // console.log("seguroDeVida: " + seguroDeVida);
      // console.log("seguroDeDanos: " + seguroDeDanos);
      // console.log("seguroDeDesempleo: " + seguroDeDesempleo);
      // console.log("Comisión: ", this.comisionAdministracion);
      // console.log("iva: ", this.iva);
      // console.log(pagoMensual + seguroDeVida + seguroDeDanos);

      // guardar el resultado en localStorage

      // var valorEscritura = this.valorDelInmueble * 0.1;
      // var conCuantoCuentas = this.apoyoState - valorEscritura;

      // var montoCredito = (valorDelInmueble * 1.1) - this.apoyoState;

      // console.log(conCuantoCuentas)

      var mensualidad =
        pagoMensual +
        seguroDeVida +
        seguroDeDanos +
        this.comisionAdministracion +
        this.iva +
        seguroDeDesempleo;

      var ingresos;
      if (this.tipoDeIngreso == "Asalariado") {
        ingresos = mensualidad * 2;
      } else {
        ingresos = mensualidad * 2.78;
      }

      this.asignarBanco({
        nombre: this.banco,
        valores: {
          porcentajeDeCredito: this.porcentajeDeCredito,
          montoDeCredito: montoDeCreditoTotal,
          mensualidad,
          ingresos,
          tasa: this.tasa,
          disponibilidad: this.disponibilidad,
          enganche: this.enganche,
          // datos de consola
          datosDeConsola: {
            tasa: this.tasa,
            montoDeCredito: montoDeCreditoTotal,
            interes: interes,
            amortizacion: amortizacion,
            pagoMensual: pagoMensual,
            seguroDeVida: seguroDeVida,
            seguroDeDanos: seguroDeDanos,
            seguroDeDesempleo: seguroDeDesempleo,
            comisión: this.comisionAdministracion,
            iva: this.iva,
            Mensualidad: mensualidad,
            valorDelInmueble: this.valorDelInmueble,
            tipoDeIngreso: this.tipoDeIngreso
          }
        }
      });

      return mensualidad;
    },
    ...mapState([
      "cuantoCuentas",
      "tipoCredito",
      "valorDelInmueble",
      "montoDeCreditoPorModificacion"
    ])
  }
};
</script>
