<template>
  <div>
    <div class="navbarFixed" v-if="navbarFixed">
      <div class="width-3 center navbar">
        <TipoDePago type="fixed"/>
        <Plazo/>
      </div>
    </div>
    <div :class="{'hide-opacity':navbarFixed}">
      <TipoDePago type="static"/>
      <Plazo/>
    </div>
  </div>
</template>
<script>
import TipoDePago from "./OpcionesEstaticas/TipoDePago";
import Plazo from "./OpcionesEstaticas/Plazo";

export default {
  components: {
    TipoDePago,
    Plazo
  },
  data() {
    return {
      scroll: 0
    };
  },
  computed: {
    navbarFixed() {
      if (this.scroll >= 290) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    scrollUpdate() {
      this.scroll = window.scrollY;
    }
  },
  mounted() {
    window.addEventListener("scroll", this.scrollUpdate, false);
  }
};
</script>
<style>
.hide-opacity {
  opacity: 0 !important;
}
</style>

