<template>
  <!-- <v-layout>
    <v-flex :xs12="statusFile === 0 || statusFile === 2" :xs10="statusFile === 1">
      <label :for="'input' +index" class="w-100">
        <div class="input-file-button d-block">{{ fieldname }}</div>
        {{statusFile}}
      </label>
      <input
        :id="'input' + index"
        type="file"
        class="input-file-hidden"
        @change="sendFile"
        accept="image/jpeg, image/gif, image/png, application/pdf"
      >
    </v-flex>
    <v-flex v-if="statusFile === 1" xs2 class="center">
      <v-progress-circular indeterminate color="primary"></v-progress-circular>
    </v-flex>
  </v-layout>-->
  <v-layout>
    <v-flex :xs9="statusFile == 1">
      <label :for="'input' +index" class="w-100">
        <div class="input-file-button d-block" :class="{'green':statusFile == 2}">
          <i class="el-icon-paperclip medium"></i>
          {{ fieldname }}
        </div>
      </label>
      <input
        :id="'input' + index"
        type="file"
        class="input-file-hidden"
        @change="pickFile"
        accept="image/jpeg, image/gif, image/png, application/pdf"
      >
    </v-flex>
    
    <v-flex v-if="statusFile == 1" xs2 class="center">
      <v-progress-circular indeterminate color="primary"></v-progress-circular>
    </v-flex>
    <v-flex v-if="statusFile == 2" xs3 class="center">
      <div title="Archivo guardado correctamente" class="input-file-button d-block green send icon">
        <i class="el-icon-circle-check large"></i>
      </div>
    </v-flex>
  </v-layout>
</template>

<script>
import Api from "@/services/api.js";
import { mapState } from "vuex";

export default {
  name: "inputFile",
  props: ["fieldname", "index"],

  data() {
    return {
      statusFile: 0,
      fileUpload: false,
      file: {}
    };
  },
  computed: {
    ...mapState(["creditoTramitado"])
  },
  methods: {
    statusChange(status) {
      this.statusFile = status;
    },
    pickFile() {
      this.file = document.getElementById("input" + this.index).files[0];
      this.fileUpload = new Date();
      console.log(this.file);
    },
    sendFile() {
      console.log(this.file);
      let data = {
        file: document.getElementById("input" + this.index).files[0],
        field: this.fieldname,
        cid: this.creditoTramitado
      };
      console.log(data);
      this.statusChange(1);

      Api.file("files/uploads.php", data, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      })
        .then(response => {
          this.statusChange(2);
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
      this.statusChange(1);
    }
  },
  watch: {
    fileUpload() {
      this.sendFile();
    }
  }
};
</script>

<style>
.input-file-button {
  background: #2096f3;
  border-radius: 5px;
  padding: 10px;
  color: #fff;
  transition: 0.5s;
  text-align: center;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.5);
  width: 100%;
  margin-bottom: 0.5rem;
}

.input-file-button.send {
  width: auto !important;
  margin-left: 20px !important;
}

.input-file-button.icon {
  padding-bottom: 4px;
  padding-top: 5px;
}

.w-100 {
  width: 100%;
}

.input-file-button:hover {
  filter: brightness(120%);
  transition: 0.5s;
}

.input-file-hidden {
  display: none;
}

.green {
  background: #4caf50 !important;
}
</style>