<template>
  <div>
    <v-text-field
      class="no-margin"
      prefix="$"
      v-model="number"
      label="Tu Ingreso Mensual"
      color="#13D8F2"
      @input="hideKeyboardStartTimeOut"
    ></v-text-field>
    <div id="ingresomensualinputkeyboard"></div>
  </div>
</template>
<script>
import { mapMutations } from "vuex";

export default {
  data() {
    return {
      ingresoMensual: "",
      number: "",
      hideKeyboardTime: ""
    };
  },
  methods: {
    currencyInput() {
      let number = parseFloat(this.number.replace(/,/g, "") || 0);
      this.ingresoMensual = number;
      this.number = this.numberWithCommas(parseFloat(number));
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },
    hideKeyboardStartTimeOut() {
      var field = document.createElement("input");
      field.setAttribute("type", "text");
      let inputContainer = document.getElementById(
        "ingresomensualinputkeyboard"
      );
      inputContainer.innerHTML = "";
      inputContainer.appendChild(field);
      inputContainer.style = "height:0px;width:100%";
      field.setAttribute("readonly", "readonly");

      clearTimeout(this.hideKeyboardTime);
      this.hideKeyboardTime = setTimeout(() => {
        // document.getElementById("toHideKeyboard").focus();
        field.focus();
        field.setAttribute("style", "display:none;width:100%");
      }, 2000);
    },
    ...mapMutations(["actualizarIngresoMensual"])
  },
  watch: {
    ingresoMensual() {
      this.actualizarIngresoMensual(this.ingresoMensual);
    },
    number() {
      this.currencyInput();
    }
  }
};
</script>

