<template>
  <v-tabs grow icons-and-text v-model="step">
    <v-tabs-slider color="#13d8f2"></v-tabs-slider>

    <!--<v-tab href="#credito" color="#13d8f2">
      Datos del Crédito
      <i class="el-icon-s-order large"></i>
    </v-tab>-->

    <v-tab href="#generales">
      Datos generales
      <i class="el-icon-s-custom large"></i>
    </v-tab>

    <v-tab href="#documentos" :disabled="disable">
      Sube tus documentos
      <i class="el-icon-upload large"></i>
    </v-tab>
    <!--<v-tab href="#tramitar">
      Buró de Crédito
      <i class="el-icon-edit large"></i>
    </v-tab>-->

    <!--<v-tab-item value="credito">
      <v-card flat class="card-no-shadow">
        <v-card-text>
          <Credito/>
        </v-card-text>
      </v-card>
    </v-tab-item>-->
    <v-tab-item value="generales">
      <v-card flat class="card-no-shadow">
        <v-card-text>
          <Generales
                  :bancoId="bancoId"
                  :banco="banco"
                  @RequestEventCloseDialog="requestCloseDialog"
                  @RequestEnableTab="requestEnableTab"/><!--AQUI ESTOY TRABAJANDO-->
        </v-card-text>
      </v-card>
    </v-tab-item>
    <v-tab-item value="documentos">
      <v-card flat class="card-no-shadow">
        <v-card-text>

          <Archivos :bancoId="bancoId" @ReturnLastTab="backLastTab"/>

        </v-card-text>
      </v-card>
    </v-tab-item>
    <!--<v-tab-item value="tramitar">
      <v-card flat class="card-no-shadow">
        <v-card-text></v-card-text>
      </v-card>
    </v-tab-item>-->
  </v-tabs>
</template>
<script>
import { mapState } from "vuex";
import Credito from "./tabs/Credito";
import Generales from "./tabs/Generales";
import Archivos from "./tabs/Archivos";
export default {
  props:["bancoId","banco"],
  components: {
    Credito,
    Generales,
    Archivos
  },
  data() {
    return {
      activeName: "first",
      step: "generales",
      disable: true
    };
  },
  computed: {
    ...mapState(["bancoSeleccionado"])
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },

    /*
     * EMITO UN EVENTO LLAMADO closingDialog PARA CERRAR EL DIALOG EN EL COMPONENTE PADRE
     */
    requestCloseDialog() {
        this.$emit('closingDialog')
    },

    requestEnableTab() {
      this.disable = false;
      this.step = "documentos"
    },
    backLastTab() {
        this.step = "generales";
    }

  }
};
</script>
<style>
.large {
  font-size: 30px;
}

.card-no-shadow.v-card.v-card--flat.v-sheet.theme--light {
  box-shadow: none;
}
</style>
