<template>
  <v-app style="background:none">
    <v-container grid-list-md text-xs-center width-3>
      <v-layout row wrap>
        <v-flex xs12>
          <v-card class="pa-4">
            <form>
              <b
                class="title font-weight-bold body-2 century-gothic"
              >Simulador de Crédito Hipotecario Express</b>
              <br>
              <TipoDeIngreso/>
              <Apoyos/>
              <IngresoMensual/>
              <ValorDelInmueble/>
              <MontoDeCredito/>
              <Gastos/>
              <OpcionesEstaticas/>
            </form>
          </v-card>
        </v-flex>
        <v-flex xs12 class="bancos-box">
          <div :key="index" v-for="(banco, index) in bancosSort">
            <BancoCard :banco="banco" :bancoId="index"/>
          </div>
        </v-flex>
      </v-layout>
    </v-container>

    <Banamex/>
    <Banorte/>
    <Banregio/>
    <Hsbc/>
    <Santander/>
    <Scotiabank/>

    <input style="display:none" id="toHideKeyboard">
  </v-app>
</template>
<script>
import TipoDeIngreso from "./components/simulador/TipoDeIngreso.vue";
import Apoyos from "./components/simulador/Apoyos.vue";
import IngresoMensual from "./components/simulador/IngresoMensual.vue";
import ValorDelInmueble from "./components/simulador/ValorDelInmueble.vue";
import MontoDeCredito from "./components/simulador/MontoDeCredito.vue";
import OpcionesEstaticas from "./components/simulador/OpcionesEstaticas.vue";
import Gastos from "./components/simulador/Gastos.vue";

//bancos

import Banamex from "./components/bancos/Banamex.vue";
import Banorte from "./components/bancos/Banorte.vue";
import Banregio from "./components/bancos/Banregio.vue";
import Hsbc from "./components/bancos/Hsbc.vue";
import Santander from "./components/bancos/Santander.vue";
import Scotiabank from "./components/bancos/Scotiabank.vue";
import BancoCard from "./components/simulador/BancoCard.vue";

import InputTest from "./InputTest.vue";

import { mapState } from "vuex";

export default {
  components: {
    TipoDeIngreso,
    Apoyos,
    IngresoMensual,
    ValorDelInmueble,
    MontoDeCredito,
    OpcionesEstaticas,
    //bancos
    Banamex,
    Banorte,
    Banregio,
    Hsbc,
    Santander,
    Scotiabank,
    BancoCard,
    Gastos,

    InputTest
  },
  data() {
    return {
      bancosSort: []
    };
  },
  computed: {
    ...mapState(["bancos"])
  },
  methods: {
    bancosSortMethod() {
      var bancosSort;
      bancosSort = this.bancos.sort((a, b) => {
        return a.valores.mensualidad - b.valores.mensualidad;
      });
      this.bancosSort = bancosSort;
    }
  },
  watch: {
    bancos() {
      setTimeout(() => {
        this.bancosSortMethod();
      }, 300);
    }
  }
};
</script>

<style>
@import url("../assets/css/style.css");
</style>
