<template>
  <v-select
    class="no-margin pt-2"
    v-model="tipoDeIngresoSeleccionado"
    :items="tiposDeIngreso"
    label="Tipo de Ingreso"
    color="#13D8F2"
  ></v-select>
</template>
<script>
import { mapMutations } from "vuex";

export default {
  data() {
    return {
      tiposDeIngreso: [
        "Asalariado",
        "Profesionista Independiente",
        "Independiente",
        "Socio de Empresa",
        "Negocio Propio"
      ],

      tipoDeIngresoSeleccionado: ""
    };
  },
  methods: {
    ...mapMutations(["actualizarTipoDeIngreso"])
  },
  watch: {
    tipoDeIngresoSeleccionado() {
      this.actualizarTipoDeIngreso(this.tipoDeIngresoSeleccionado);
    }
  }
};
</script>
