import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {

    // backend: "http://localhost/gestion-de-creditos/server/", // chris dev
    backend: "http://localhost:8888/gestion-de-creditos/server/", // emipmt dev
    // backend: "http://localhost:8888/gestion-de-creditos/server/", // e dev
    // backend: "http://api-creditohipotecarioexpress.esy.es/server/", // dist

    productoSeleccionado: "Comprar mi Casa",
    bancos: [],

    cuantoCuentas: "",
    tipoCredito: "",

    tipoDeIngreso: "",
    ingresoMensual: "",
    valorDelInmueble: "",
    montoDeCredito: "",

    modificarMontoDeCredito: false,
    montoDeCreditoPorModificacion: undefined,

    montoDeCreditoMaximoFicitcio: "",

    tipoDePagoSeleccionado: 0,
    plazoSeleccionado: "2",

    gastos: "",

    bancoSeleccionado: "",

    //creditoTramitado es el id que se va a signar mientras el usuario está tramitando su crédito
    creditoTramitado: "",

  },
  mutations: {
    asignarBanco(state, banco) {
      let existe = false;
      state.bancos.forEach((bancoState, index) => {
        if (bancoState.nombre === banco.nombre) {
          existe = true;
          state.bancos[index] = banco;
        }
      });

      if (!existe) state.bancos.push(banco);

    },
    actualizarApoyo(state, apoyo) {
      state.cuantoCuentas = apoyo;
    },
    actualizarTipoDeIngreso(state, tipoDeIngreso) {
      state.tipoDeIngreso = tipoDeIngreso;
    },
    actualizarIngresoMensual(state, ingresoMensual) {
      state.ingresoMensual = ingresoMensual;
    },
    actualizarValorDelInmueble(state, valorDelInmueble) {
      state.valorDelInmueble = valorDelInmueble;
    },
    actualizarMontoDeCredito(state, montoDeCredito) {
      state.montoDeCredito = montoDeCredito;
    },

    actualizarModificarMontoDeCredito(state, modificarMontoDeCredito) {
      state.modificarMontoDeCredito = modificarMontoDeCredito;
    },
    actualizarMontoDeCreditoPorModificacion(state, montoDeCreditoPorModificacion) {
      state.montoDeCreditoPorModificacion = montoDeCreditoPorModificacion;
    },

    actualizarMontoDeCreditoMaximoFicitcio(state, montoDeCreditoMaximoFicitcio) {
      state.montoDeCreditoMaximoFicitcio = montoDeCreditoMaximoFicitcio;
    },

    actualizarTipoDePagoSeleccionado(state, tipoDePagoSeleccionado) {
      state.tipoDePagoSeleccionado = tipoDePagoSeleccionado;
    },

    actualizarPlazoSeleccionado(state, plazoSeleccionado) {
      state.plazoSeleccionado = plazoSeleccionado;
    },

    actualizarTipoCredito(state, tipoCredito) {
      state.tipoCredito = tipoCredito;
    },

    actualizarGastos(state, gastos) {
      state.gastos = gastos;
    },

    actualizarBancoSeleccionado(state, banco) {
      state.bancoSeleccionado = banco;
    },

    actualizarCreditoTramitado(state, id) {
      state.creditoTramitado = id;
    }

  },

})