<template>
  <v-layout>
    <v-form ref="form">
      <v-dialog  v-model="dialog" persistent width="700"  fullscreen>
        <v-card>
          <v-layout row wrap>
            <v-flex class="width-3 center-margin pt-3">
              <!-- <div class="right-align">
                <img :src="require('@/assets/credito-invert.png')" alt>
              </div>-->
              <div v-if="status == 0" class="headline center">
                <b style="font-size:20px">Tu Crédito autorizado de 24 a 48 hrs</b>
              </div>
              <div v-if="status == 1" class="headline center">
                <b style="font-size:20px">Firma tu escritura en 3 o 4 semanas</b>
              </div>
              <v-card-title
                v-if="status == 2"
                class="headline"
              >Tramitar tu crédito en linea es fácil y rápido, regístrate.</v-card-title>
              <v-card-text style="padding-top: 0; padding-bottom: 0">
                <div class="center-tab">
                  <div v-if="status == 0" class="simulate-tab">
                    <i class="el-icon-share large"></i>
                    <br>COMPARTIR CON
                  </div>

                  <div v-if="status == 1" class="simulate-tab">
                    <i class="el-icon-question large"></i>
                    <br>INFORMES
                  </div>
                </div>

                <v-container style="padding-top: 0; padding-bottom: 0">
                  <v-layout wrap>
                    <v-flex xs12>
                      <v-text-field
                        color="#13D8F2"
                        label="Nombre"
                        :rules="[rules.required]"
                        v-model="name"
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field
                        color="#13D8F2"
                        label="Celular"
                        type="tel"
                        v-model="phone"
                        :rules="[rules.required, rules.number]"
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field
                        color="#13D8F2"
                        label="Correo electrónico"
                        type="email"
                        :rules="[rules.required, rules.email]"
                        v-model="email"
                      ></v-text-field>
                    </v-flex>

                    <v-flex xs12 v-if="status == 1">
                      <v-textarea
                        color="#13D8F2"
                        label="Mensaje"
                        type="message"
                        :rules="[rules.required]"
                        v-model="message"
                      ></v-textarea>
                    </v-flex>

                    <v-flex xs12 v-if="status == 2">
                      <v-text-field
                        color="#13D8F2"
                        label="Contraseña"
                        :type="show ? 'text' : 'password'"
                        :append-icon="show ? 'visibility' : 'visibility_off'"
                        @click:append="show = !show"
                        :rules="[rules.required]"
                        v-model="password"
                      ></v-text-field>
                    </v-flex>
                  </v-layout>

                  <v-alert
                    v-model="alert"
                    type="success"
                    dismissible
                    transition="scale-transition"
                  >{{ response }}</v-alert>
                </v-container>
              </v-card-text>
              <v-card-actions style="padding-right: 40px; padding-bottom:25px">
                <v-spacer></v-spacer>
                <v-btn color="#13D8F2" @click="closeDialog">Volver</v-btn>
                <v-btn :disabled="sending" color="#13D8F2" @click="signUp">Enviar</v-btn>
              </v-card-actions>
            </v-flex>
          </v-layout>
        </v-card>
      </v-dialog>
    </v-form>
  </v-layout>
</template>

<script>
import Api from "@/services/api.js";
import { mapState } from "vuex";
export default {
  props: ["dialog", "status", "banco", "creditoPreautorizado"],
  data() {
    return {
      // datos del usuario
      name: "",
      email: "",
      phone: "",
      password: "",

      message: "",
      alert: false,
      show: false,

      rules: {
        required: value => !!value || "Requerido.",
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || "Correo no valido.";
        },
        number: value => {
          const pattern = /^([0-9])*$/;
          return pattern.test(value) || "Teléfono invalido.";
        }
      },

      // datos de envío

      sending: false,
      response: "",
      step: 0
    };
  },
  computed: {
    ...mapState([
      "tipoDeIngresoSeleccionado",
      "ingresoMensual",
      "bancos",
      "backend",
      "tipoDePagoSeleccionado",
      "plazoSeleccionado",
      "cuantoCuentas",
      "tipoCredito",
      "valorDelInmueble"
    ])
  },
  methods: {
    signUp() {
      if (this.$refs.form.validate()) {
        this.sending = true;

        let data = {
          // datos del usuario
          name: this.name,
          email: this.email,
          phone1: this.phone,
          status: this.status,
          message: this.message,
          type: 0,
          password: this.password,
          date: new Date(),

          // datos del credito

          monthlyPayment: this.banco.valores.mensualidad,
          earnings: this.banco.valores.ingresos,
          mortgageRate: this.banco.valores.tasa,
          requestedCredit: this.banco.valores.montoDeCredito,
          preAuthorizedCredit: this.creditoPreautorizado,
          propertyCost: this.valorDelInmueble,
          bank: this.banco.nombre,
          paymentType: this.tipoDePagoSeleccionado,
          lapse: this.plazoSeleccionado,
          support: this.cuantoCuentas,
          supportType: this.tipoCredito
        };

        Api.post("transact/create.php", data).then(response => {
          console.log(response.data);
          if (this.status == 1) {
            this.sending = false;
            this.alert = true;
            // this.closeDialog();
            this.response = response.data.message;
          } else if (this.status == 0) {
            this.$emit("share");
            this.sending = false;
          }
        });
      }
    },
    closeDialog() {
      this.$emit("closeDialog");
    }
  },

  mounted() {
    this.step = 1;
    this.$forceUpdate();
  }
};
</script>
<style>
.simulate-tab {
  text-align: center;
  font-weight: bold;
  width: 50%;
  padding: 10px 0;
  margin: 20px auto;
  border-bottom: solid 2px #13d8f2;
}

.center {
  text-align: center !important;
  margin-left: auto;
}

.center-tab {
  text-align: center;
}
</style>

<style scoped>
.headline {
  color: #13d8f2;
  font-weight: bold;
  padding: 0px !important;
}

.center-margin {
  margin: 0 auto;
}

.right-align img {
  margin-right: 10%;
}
</style>
