<template>
  <div>
    <v-card
      class="pa-4 mb-1 padding-bottom-responsive no-shadow f2f2f2"
      style="padding-top: 0 !important; "
      v-if="banco.valores.disponibilidad"
    >
      <v-card-text class="pa-0 mb-0">
        <!-- <v-container bg fill-height grid-list-md text-xs-center> -->
        <v-container grid-list-md pa-0 fill-height text-xs-center>
          <v-layout row wrap align-center>
            <v-flex xs12 sm12 md3>
              <img :src="require('@/assets/logos/'+imagenesDeBancos[banco.nombre])">
            </v-flex>
            <v-flex xs12 sm9 md6 align-center>
              <div class="left-align">
                <strong>
                  <span class="float-left">Pago Mensual:</span>
                  <span class="float-right">{{banco.valores.mensualidad.toFixed(2) | currency}}</span>
                </strong>
                <br>

                <div>
                  <span class="float-left">Ingresos:</span>
                  <span class="float-right">{{banco.valores.ingresos.toFixed(2) | currency}}</span>
                </div>
                <br>
                <div>
                  <span class="float-left">Tasa fija anual:</span>
                  <span class="float-right">{{(banco.valores.tasa * 100).toFixed(2)}} %</span>
                </div>
                <br>

                <div>
                  <span class="float-left">Crédito Solicitado:</span>
                  <span class="float-right">
                    {{(banco.valores.porcentajeDeCredito * 100).toFixed(2)}} %
                    <span
                      class="pl-2"
                    >{{banco.valores.montoDeCredito.toFixed(2) | currency}}</span>
                  </span>
                </div>

                <br>
                <strong v-show="creditoPreautorizado">
                  <span class="float-left">Crédito Pre autorizado:</span>
                  <span class="float-right">{{creditoPreautorizado.toFixed(2) | currency}}</span>
                </strong>
                <br> 
              </div>
            </v-flex>
            <v-flex xs12 sm3 md3>
              <v-layout row wrap align-center>
                <v-flex xs4 sm12>
                  <Info
                    class="small-button"
                    :banco="banco"
                    :creditoPreautorizado="creditoPreautorizado"
                  />
                </v-flex>
                <v-flex xs4 sm12>
                  <Compartir
                    class="small-button"
                    :banco="banco"
                    :creditoPreautorizado="creditoPreautorizado"
                    :bancoId="bancoId"
                  />
                </v-flex>
                <v-flex xs4 sm12>
                  <Tramitar
                    class="small-button"
                    :click="actualizarBancoSeleccionado(banco)"
                    :banco="banco"
                    :creditoPreautorizado="creditoPreautorizado"
                    :bancoId="bancoId"
                  />
                </v-flex>
              </v-layout>
            </v-flex>
          </v-layout>
        </v-container>

        <!-- <div style="text-align:left">
                  <div>Aforo: {{(banco.valores.porcentajeDeCredito * 100).toFixed(2)}} %</div>
                  <div>Credito:  {{banco.valores.montoDeCredito.toFixed(2)}}</div>
                  <br>
                  <div>Mensualidad:  {{banco.valores.mensualidad.toFixed(2)}}</div>
                  <div>Ingresos:  {{banco.valores.ingresos.toFixed(2)}}</div>
                  <div>Tasa: {{(banco.valores.tasa * 100).toFixed(2)}} %</div>
                  <div
                    v-show="creditoPreautorizado"
                  >Crédito Preautorizado:  {{creditoPreautorizado.toFixed(2)}}</div>
                  <hr> enganche: {{banco.valores.enganche}}
        </div>-->

        <div v-if="debug" class="left-align debug">
          <b>{{banco.nombre}}</b>
          <br>Tasa:
          <b>{{banco.valores.datosDeConsola.tasa | currency}}</b>
          <br>Monto De Crédito:
          <b>{{banco.valores.datosDeConsola.montoDeCredito | currency}}</b>
          <br>Interes:
          <b>{{banco.valores.datosDeConsola.interes | currency}}</b>
          <br>Amortizacion:
          <b>{{banco.valores.datosDeConsola.amortizacion | currency}}</b>
          <br>Pago mensual:
          <b>{{banco.valores.datosDeConsola.pagoMensual | currency}}</b>
          <br>Seguro de Vida:
          <b>{{banco.valores.datosDeConsola.seguroDeVida | currency}}</b>
          <br>Seguro de Daños:
          <b>{{banco.valores.datosDeConsola.seguroDeDanos | currency}}</b>
          <br>Seguro de Desempleo:
          <b>{{banco.valores.datosDeConsola.seguroDeDesempleo | currency}}</b>
          <br>Comisión:
          <b>{{banco.valores.datosDeConsola.comisión | currency}}</b>
          <br>Iva:
          <b>{{banco.valores.datosDeConsola.iva | currency}}</b>
          <br>Mensualidad:
          <b>{{banco.valores.datosDeConsola.Mensualidad | currency}}</b>
          <br>
        </div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import Info from "./actionButtons/Info.vue";
import Compartir from "./actionButtons/Share.vue";
import Tramitar from "./actionButtons/Transact.vue";

export default {
  components: {
    Info,
    Compartir,
    Tramitar
  },
  props: ["banco", "bancoId"],
  data() {
    return {
      imagenesDeBancos: {
        banamex: "logo-banamex.gif",
        banorte: "logo-banorte.gif",
        banregio: "logo-banregio.gif",
        hsbc: "logo-hsbc.gif",
        santander: "logo-santander.gif",
        scotiabank: "logo-scotiabank.gif"
      },

      debug: false
    };
  },
  computed: {
    creditoPreautorizado() {
      if (this.tipoDeIngreso == "Asalariado") {
        return (parseFloat(this.ingresoMensual) / 2 - this.gastos) * 100;
      } else {
        return (parseFloat(this.ingresoMensual) * 0.36 - this.gastos) * 100;
      }
    },
    ...mapState(["tipoDeIngreso", "ingresoMensual", "bancos", "gastos"])
  },
  methods: {
    ...mapMutations(["actualizarBancoSeleccionado"])
  },
  watch: {
    bancos() {
      this.$forceUpdate();
    }
  }
};
</script>
<style>
.debug {
  background: #333;
  color: #41bcdb;
  border-radius: 5px;
  padding: 5px 10px;
}

.debug b {
  color: rgb(248, 255, 255);
}

