<template>
  <div>
    <v-btn
      v-if="this.tipoDeIngreso &&
        this.ingresoMensual &&
        this.valorDelInmueble"
      block
      flat
      small
      style="color:#333; margin: 0 auto"
      class="hover-invert"
      @click="transact"
    >Tramitar</v-btn>

    <v-tooltip bottom v-else>
      <template v-slot:activator="{ on }">
        <v-btn
          block
          disabled
          flat
          small
          style="color:#333; margin: 0 auto"
          class="hover-invert disabled"
          v-on="on"
        >Tramitar</v-btn>
      </template>
      <span>Completa todos los campos</span>
    </v-tooltip>

    <ModalTransact
      :dialog="dialog"
      @closeDialog="dialog = false"
      status="2"
      :banco="banco"
      :creditoPreautorizado="creditoPreautorizado"
      :bancoId="bancoId"
    />
  </div>
</template>

<script>
import { mapState } from "vuex";
import ModalTransact from "./ModalTransact.vue";
export default {
  props: ["banco", "creditoPreautorizado", "bancoId"],

  components: {
    ModalTransact
  },
  data() {
    return {
      dialog: false
    };
  },
  computed: {
    ...mapState([
      "tipoDeIngreso",
      "ingresoMensual",
      "valorDelInmueble",
      "gastos"
    ])
  },
  methods: {
    transact() {
      this.dialog = true;
    }
  }
};
</script>
