<template>
  <div>
    <v-text-field v-model="number" label="Regular"></v-text-field>
  </div>
</template>
<script>
export default {
  data() {
    return {
      number: "",
      numberParsed: ""
    };
  },
  methods: {
    set() {
			let number = parseFloat(this.number.replace(/,/g, ''));
			this.numberParsed = number;
			this.number = parseFloat(number).toLocaleString();
		}
  },
  watch: {
    number() {
      this.set();
    }
  }
};
</script>
