<template>
  <div>
    <v-btn
      block
      flat
      small
      color="#"
      style="color:#333; margin: 0 auto"
      class="hover-invert"
      @click="dialog = true"
    >Compartir</v-btn>

    <signUp
      :dialog="dialog"
      @closeDialog="dialog = false"
      status="0"
      :banco="banco"
      :creditoPreautorizado="creditoPreautorizado"
      @share="share = true"
    />

    <v-layout>
      <v-dialog v-model="share" persistent max-width="300px">
        <v-card>
          <v-form ref="form">
            <v-card-title class="headline">Compartir</v-card-title>
            <v-card-text>
              <v-container>
                <v-layout wrap>
                  <v-flex xs12 class="center">
                    <a
                      :href="'whatsapp://send?text='+ shareText('whatsapp')"
                      data-action="share/whatsapp/share"
                    >
                      <v-btn fab dark large color="dark">
                        <img style="width:40px" :src="require('@/assets/logos/whatsapp-white.png')">
                      </v-btn>
                    </a>
                  </v-flex>
                  <v-flex xs12 class="center">
                    <a
                      :href="'mailto:alguien@ejemplo.com?Subject=Simulador%20de%20crédito%20hipotecario%20express&body='+ shareText('email')"
                    >
                      <v-btn fab dark large color="dark">
                        <img style="width:40px" :src="require('@/assets/logos/mail-white.png')">
                      </v-btn>
                    </a>
                  </v-flex>

                  <v-flex xs12 class="center">
                    <a href="tel:2221903333">
                      <v-btn fab dark large color="dark">
                        <v-icon dark>phone</v-icon>
                      </v-btn>
                    </a>
                  </v-flex>
                  <!-- <v-flex xs12 class="center">
                    <v-btn @click="shareFunction" fab dark large color="blue">
                      <v-icon dark>share</v-icon>
                    </v-btn>
                  </v-flex>-->
                </v-layout>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="share = false">Cancelar</v-btn>
            </v-card-actions>
          </v-form>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>

<script>
import SignUp from "./SignUp.vue";
export default {
  props: ["banco", "bancoId", "creditoPreautorizado"],

  components: {
    SignUp
  },
  data() {
    return {
      dialog: false,
      share: false
    };
  },
  computed: {},
  methods: {
    shareText(platform) {
      let platforms = {
        whatsapp: `Tramita este crédito en https://creditohipotecarioexpress.com %0A%0ACrédito Hipotecario en *${this.banco.nombre.toUpperCase()}* %0AMensualidad: *${this.$options.filters.currency(
          this.banco.valores.mensualidad.toFixed(2)
        )}* %0AIngresos: *${this.$options.filters.currency(
          this.banco.valores.ingresos.toFixed(2)
        )}* %0ATasa fija anual: *${(this.banco.valores.tasa * 100).toFixed(
          2
        )} %* %0ACrédito Solicitado: *${this.$options.filters.currency(
          this.banco.valores.montoDeCredito.toFixed(2)
        )}* %0ACrédito Pre autorizado: *${this.$options.filters.currency(
          this.creditoPreautorizado.toFixed(2)
        )}* %0A%0A `,
        email: `Tramita este crédito en https://creditohipotecarioexpress.com %0A%0ACrédito Hipotecario en ${this.banco.nombre.toUpperCase()} %0A Mensualidad: ${this.$options.filters.currency(
          this.banco.valores.mensualidad.toFixed(2)
        )} %0A Ingresos: ${this.$options.filters.currency(
          this.banco.valores.ingresos.toFixed(2)
        )} %0A Tasa fija anual: ${(this.banco.valores.tasa * 100).toFixed(
          2
        )} %0A Crédito Solicitado: ${this.$options.filters.currency(
          this.banco.valores.montoDeCredito.toFixed(2)
        )} %0A Crédito Pre autorizado: ${this.$options.filters.currency(
          this.creditoPreautorizado.toFixed(2)
        )}`,
        share: `
        
        Tramita este crédito en https://creditohipotecarioexpress.com

        Crédito Hipotecario en ${this.banco.nombre.toUpperCase()} 
        
        Mensualidad: ${this.$options.filters.currency(
          this.banco.valores.mensualidad.toFixed(2)
        )}
        Ingresos: ${this.$options.filters.currency(
          this.banco.valores.ingresos.toFixed(2)
        )}
        Tasa fija anual: ${(this.banco.valores.tasa * 100).toFixed(2)} 
        Crédito Solicitado: ${this.$options.filters.currency(
          this.banco.valores.montoDeCredito.toFixed(2)
        )} 
        Crédito Pre autorizado: ${this.$options.filters.currency(
          this.creditoPreautorizado.toFixed(2)
        )} 

        `
      };
      return platforms[platform];
    },
    shareFunction() {
      navigator.share({
        title: "Simulador de crédito hipotecario express",
        text: this.shareText("share")
      });
    }
  },
  mounted() {}
};
</script>
<style>
a {
  text-decoration: none !important;
}
</style>
