<template>
  <form @submit.prevent="next">
    <v-layout wrap>
      <v-flex xs12 lg6 style="padding:5px">
        <v-text-field required color="#13D8F2" label="Nombre (s)" v-model="name1"></v-text-field>
      </v-flex>
      <v-flex xs12 lg6 style="padding:5px">
        <v-text-field required color="#13D8F2" label="Apellido Paterno" v-model="name2"></v-text-field>
      </v-flex>
      <v-flex xs12 lg6 style="padding:5px">
        <v-text-field required color="#13D8F2" label="Apellido Materno" v-model="name3"></v-text-field>
      </v-flex>
      <v-flex xs12 lg6 style="padding:5px">
        <v-text-field required color="#13D8F2" label="Edad" v-model="age"></v-text-field>
      </v-flex>
      <v-flex xs12 lg6 style="padding:5px">
        <div class="input-date-container">
          <input
            required
            @input="handlerDateInput('birthdateDay','birthdateMonth' + bancoId,2)"
            v-model="birthdateDay"
            maxlength="2"
            type="text"
            class="input-date"
            placeholder="DD"
          >/
          <input
            required
            @input="handlerDateInput('birthdateMonth','birthdateYear' + bancoId,2)"
            v-model="birthdateMonth"
            :id="'birthdateMonth'+ bancoId"
            maxlength="2"
            type="text"
            class="input-date"
            placeholder="MM"
          >/
          <input
            required
            v-model="birthdateYear"
            :id="'birthdateYear'+ bancoId"
            maxlength="4"
            type="text"
            class="input-date"
            placeholder="AAAA"
          >
        </div>
      </v-flex>
      <v-flex xs12 lg6 style="padding:5px">
        <v-text-field required color="#13D8F2" label="Nacionalidad" v-model="nationality"></v-text-field>
      </v-flex>
      <v-flex xs12 lg6 style="padding:5px">
        <v-text-field required color="#13D8F2" label="RFC" v-model="rfc"></v-text-field>
      </v-flex>
      <v-flex xs12 lg6 style="padding:5px">
        <v-text-field required color="#13D8F2" label="CURP" v-model="curp"></v-text-field>
      </v-flex>
      <v-flex xs12 lg6 style="padding:5px">
        <v-text-field
          required
          color="#13D8F2"
          minlength="10"
          maxlength="10"
          label="Número Celular"
          v-model="phone1"
        ></v-text-field>
      </v-flex>
      <v-flex xs12 lg6 style="padding:5px">
        <v-text-field
          required
          color="#13D8F2"
          minlength="10"
          maxlength="10"
          label="Número Telefonico"
          v-model="phone2"
        ></v-text-field>
      </v-flex>
      <v-flex xs12 lg6 style="padding:5px">
        <v-text-field required color="#13D8F2" type="email" label="Correo" v-model="email"></v-text-field>
      </v-flex>
      <v-flex xs12 lg6 style="padding:5px">
        <v-text-field
          :type="showPassword ? 'text' : 'password'"
          :append-icon="showPassword ? 'visibility' : 'visibility_off'"
          @click:append="showPassword = !showPassword"
          required
          color="#13D8F2"
          label="Crear Contraseña"
          v-model="password"
        ></v-text-field>
      </v-flex>
      <v-flex xs12 lg6 style="padding:5px">
        <v-text-field
          :type="showPassword ? 'text' : 'password'"
          :append-icon="showPassword ? 'visibility' : 'visibility_off'"
          @click:append="showPassword = !showPassword"
          required
          color="#13D8F2"
          label="Confirmar Contraseña"
          v-model="passwordConfirm"
        ></v-text-field>
      </v-flex>
      <v-flex xs12>
        <v-alert v-model="alertWarning" dismissible type="warning">{{alertMessage}}</v-alert>
      </v-flex>
      <v-flex>
        <v-card-actions style="padding-right: 40px;">
          <v-spacer></v-spacer>
          <!--
            EMITO UN EVENTO AL COMPONENTE PADRE: ESTOY EN UN COMPONTENTE "NIETO"
            POR LO QUE ESTE EMIT ENCADENARA OTRO EMIT PARA QUE EL COMPONENTE PADRE
            CAPTURE ESTE EVENTO Y CIERRE EL DIALOG
          -->
          <v-btn color="#13D8F2" @click="$emit('RequestEventCloseDialog')">Volver</v-btn>
          <v-btn type="submit" color="#13D8F2">Siguiente</v-btn>
        </v-card-actions>
      </v-flex>
    </v-layout>
  </form>
</template>

<script>
import Api from "@/services/api.js";
import { mapState, mapMutations } from "vuex";
export default {
  props: ["bancoId", "banco"],
  data() {
    return {
      // datos del usuario
      name1: "",
      name2: "",
      name3: "",
      age: "",
      birthdateDay: "",
      birthdateMonth: "",
      birthdateYear: "",
      nationality: "",
      rfc: "",
      curp: "",
      email: "",
      phone1: "",
      phone2: "",
      password: "",
      passwordConfirm: "",

      message: "",
      alert: false,
      show: false,
      rules: {
        required: value => !!value || "Requerido.",
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || "Correo no valido.";
        },
        number: value => {
          const pattern = /^([0-9])*$/;
          return pattern.test(value) || "Teléfono invalido.";
        }
      },

      showPassword: false,
      sending: false,
      response: "",

      alertMessage: "",
      alertWarning: false
    };
  },
  computed: {
    ...mapState(["valorDelInmueble"])
  },
  methods: {
    handlerDateInput(thisId, toId, length) {
      if (this[thisId].length == length) {
        document.getElementById(toId).focus();
      }
    },
    next() {
      this.sending = true;

      let data = {
        // datos del usuario
        name: `${this.name1} ${this.name2} ${this.name3}`,
        age: this.age,
        birthdate: `${this.birthdateDay}/${this.birthdateMonth}/${
          this.birthdateYear
        }`,
        nationality: this.nationality,
        rfc: this.rfc,
        curp: this.curp,
        email: this.email,
        phone1: this.phone1,
        phone2: this.phone2,
        password: this.password,
        passwordConfirm: this.passwordConfirm,
        type: 0,

        date: new Date(),
        status: 2,

        // datos del credito

        monthlyPayment: this.banco.valores.mensualidad,
        earnings: this.banco.valores.ingresos,
        mortgageRate: this.banco.valores.tasa,
        requestedCredit: this.banco.valores.montoDeCredito,
        preAuthorizedCredit: this.creditoPreautorizado,
        bank: this.banco.nombre,
        paymentType: this.tipoDePagoSeleccionado,
        propertyCost: this.valorDelInmueble,
        lapse: this.plazoSeleccionado,
        support: this.cuantoCuentas,
        supportType: this.tipoCredito
      };

      if (this.password !== this.passwordConfirm) {
        this.alertMessage =          "Crear contraseña y confirmar contraseña deben ser iguales.";
        this.alertWarning = true;
      } else {
        Api.post("transact/create.php", data).then(response => {
          console.log(data);
          console.log(response.data);
          this.sending = false;
          this.alert = true;
          this.actualizarCreditoTramitado(response.data.credit.id);
          this.response = response.data.message;
          this.tabs = 1;
          if (response.data.status) {
            this.$emit("RequestEnableTab");
            this.toTop();
          }
        });
      }
    },
    toTop() {
      document
        .querySelector(
          ".v-dialog.v-dialog--active.v-dialog--persistent.v-dialog--fullscreen"
        )
        .scrollTo(0, 0);
    },
    ...mapMutations(["actualizarCreditoTramitado"])
  }
};
</script>
<style>
.input-date {
  width: 30%;
  padding: 13px 10px;
  text-align: center;
  outline: none;
}
.input-date-container {
  border: none;
  border-bottom: 2px solid #333;
  text-align: center;
}
</style>

